.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.left-margin {
  float: left;
  margin: 3%;
}

.App-logo {
  /* height: 20vmin; */
  width: 90%;
  max-width: 500px;
  padding: 20px;
}

.App-header {
  background-color: white;
  font-family: Verdana, Geneva, sans-serif;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  font-size: 3rem;
  color: #01344a;
  padding: 30px;
}

.App-title {
  font-size: 2rem;
  font-weight: bold;
  align-items:  unset;
}

.App-title-dark {
  font-size: 1.6rem;
  font-weight: bold;
  align-items:  unset;
  background: #0098bc;
  color: white;
}
.App-dark {
  background: #0098bc;
  color: white;
  padding: 50px;
  font-size: 1.6rem;
}
.App-info {
  background-color: white;
  font-family: Verdana, Geneva, sans-serif;
  align-items: center;
  flex-direction: column;
  justify-content: stretch;
  /* font-size: calc(18px); */
  color: #01344a;
  padding: 50px;
}

.App-proj {
  background-color: white;
  font-family: Verdana, Geneva, sans-serif;
  align-items: left;
  flex-direction: column;
  justify-content: stretch;
  /* font-size: calc(18px); */
  color: #0098bc;
  border: 1px #0098bc solid;
  padding: 20px;
  margin-bottom: 5rem;
}

.App-project {
  align-items: center;
}

.App-separator {
  background-color: white;
  color: #ea5a0b;
  font-style: oblique;
  font-weight: bold;
  font-size: 1.6rem;
  padding: 50px;
}

.App-contributors {
  background-color: white;
  /* opacity: 0.6; */
  color: #01344a;
  font-style: oblique;
  font-weight: bold;
  font-size: 1.5rem;
  padding: 50px;
}

.App-link {
  color: #ea5a0b;
}

.LanguageSelect {
  align-items: right;
  flex-direction: column;
}

